        .then(
          response => {
            this.setCurrentPriceAndValuation(response.data.last, element, false),
            this.brStockCount = this.brStockCount + 1;
            this.brStocksValuationSum = parseFloat(this.brStocksValuationSum) + parseFloat(element.valuation)
